.el-select-group {
  margin: 0;
  padding: 0; }

.el-select-group__wrap {
  position: relative;
  list-style: none;
  margin: 0;
  padding: 0; }

.el-select-group__wrap:not(:last-of-type) {
  padding-bottom: 24px; }

.el-select-group__wrap:not(:last-of-type)::after {
  content: '';
  position: absolute;
  display: block;
  left: 20px;
  right: 20px;
  bottom: 12px;
  height: 1px;
  background: #e4e7ed; }

.el-select-group__title {
  padding-left: 20px;
  font-size: 12px;
  color: #909399;
  line-height: 30px; }

.el-select-group .el-select-dropdown__item {
  padding-left: 20px; }
